<template>
  <!-- class markdown-body 
  <div class="markdown-body">
    <markdown />
  </div>-->
  <div v-html="md" class = "markdown-body"></div>
</template>

<script class="hljs" >
  const marked = require('marked')
  import document from '../markdown/computer.md'

  // 
  import 'highlight.js/styles/github.css'
  //  github 
  import 'github-markdown-css'

  export default {
    name :"Markdown",
    data() {
      return {
        docName : "",
        md : ''
      }
    },
    created() {
      // markedhelloMarkedhtml
      this.md = marked(document)
    },

  }
</script>